<template>
  <footer class="footer">
    <nav class="footer__navigation">
      <ul class="footer__navigation__list">
        <li
          v-for="item of links"
          :key="item.key"
          class="footer__navigation__list__item"
        >
          <a @click="linkClick(item)">{{ item.title }}</a>
        </li>
      </ul>
    </nav>
    <div class="footer__tools">
      <a
        v-for="item of tools"
        :key="item.key"
        class="footer__tools__item"
        :href="item.href"
      >
        <Icon :name="item.icon"/>
      </a>
      <Lang show-arrow bordered box-shadow />
    </div>
  </footer>
</template>

<script>
import { reactive, computed } from 'vue'
import { useRouter } from 'vue-router'
import { useI18n } from '@/shared/plugins/i18n'
import Icon from '@/shared/components/Icon/Icon'
import Lang from '@/shared/components/UIKit/Lang'
import { MAIN_ROUTES_NAMES } from '@/shared/common/routes'

export default {
  name: 'Footer',
  components: { Icon, Lang },
  setup () {
    const { t } = useI18n()
    const router = useRouter()
    const linkClick = item => {
      if (item.to) {
        router.push({ name: item.to })
      }
    }

    const links = computed(() => ([
      { title: t('footer.aboutUs'), key: 'about', to: MAIN_ROUTES_NAMES.ABOUT_US },
      { title: 'AML/FAQ', key: 'faq', to: MAIN_ROUTES_NAMES.FAQ },
      { title: t('footer.news'), key: 'news', to: MAIN_ROUTES_NAMES.NEWS }
    ]))
    const tools = reactive([
      { title: 'Twitter', icon: 'twitter', key: 'twitter', href: 'https://twitter.com/' },
      { title: 'Telegram', icon: 'telegram', key: 'telegram', href: 'https://web.telegram.org/' }
    ])

    return {
      links,
      tools,
      linkClick
    }
  }
}
</script>

