<template>
  <header class="header">
    <div class="header__logo">
      <Icon
        @click="logoClick"
        class="header__logo-icon"
        name="logo-icon"
      />
    </div>
    <div class="header__description">
      <p>{{ t('header.logoTitle') }}</p>
    </div>
    <Loader v-show ="isLoading" class="header__loaderBtn"/>
  </header>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import { useRouter } from 'vue-router'
import { useGetters } from '@/shared/plugins/vuex'
import { MAIN_ROUTES_NAMES } from '@/shared/common/routes'
import { MODULES_NAMES } from '@/shared/common/modules'
import Icon from '@/shared/components/Icon/Icon'
import Loader from '@/shared/components/UIKit/Loader/LoaderBtn'
export default {
  name: 'Header',
  components: { Icon, Loader },
  setup () {
    const router = useRouter()
    const { t } = useI18n()
    const {
      getIsLoading: isLoading
    } = useGetters(MODULES_NAMES.LOADING)

    const logoClick = () => {
      router.push({ name: MAIN_ROUTES_NAMES.HOME })
    }
    return {
      t,
      isLoading,
      logoClick
    }
  }
}
</script>

